import * as d3 from 'd3';
import '@/assets/global.css';
import { segmentHeights, segments, segmentWidths, widthSegments, areaSizes, areaSegments } from '@/services/sizeMapping';

export function HeightLegendChart(containerSelector) {
  const width = 330;
  const height = 455; // 
  const margin = { top: 40, right: 20, bottom: 60, left: 80 }; // 

  //  SVG 
  const svg = d3.select(containerSelector)
    .append("svg")
    .attr("width", width + margin.left + margin.right)
    .attr("height", height + margin.top + margin.bottom)
    .append("g")
    .attr("transform", `translate(${margin.left},${margin.top})`);

  // 
  const numLayers = 5;
  const barsPerLayer = Math.ceil(segmentHeights.length / numLayers); // 

  // 
  const layers = Array.from({ length: numLayers }, (_, layerIndex) => {
    return {
      heights: segmentHeights.slice(layerIndex * barsPerLayer, (layerIndex + 1) * barsPerLayer),
      segments: segments.slice(layerIndex * barsPerLayer, (layerIndex + 1) * barsPerLayer),
      indices: Array.from({ length: barsPerLayer }, (_, i) => i + 1)
    };
  });

  // 
  const x = d3.scaleBand()
    .domain(layers[0].indices) // 
    .range([0, width])
    .padding(0.1);

  const y = d3.scaleLinear()
    .domain([0, d3.max(segmentHeights)])
    .range([height, 0]);

  // 
  const xAxis = d3.axisBottom(x)
    .tickFormat(() => "");  // 

  const yAxis = d3.axisLeft(y);

  //  SVG
  svg.append("g")
    .attr("class", "y-axis")
    .call(yAxis);

  //  y  SVG 
  svg.append("text")
    .attr("class", "y-axis-title")
    .attr("x", -margin.left / 2) //  y 
    .attr("y", -20) // 
    .style("text-anchor", "start")
    .style("font-size", "12px")
    .text("Mapping height (unit: px)");

  //  SVG
  const xAxisGroup = svg.append("g")
    .attr("class", "x-axis")
    .attr("transform", `translate(0,${height})`)
    .call(xAxis);

  //  x  SVG 
  svg.append("text")
    .attr("class", "x-axis-title")
    .attr("x", width / 2)
    .attr("y", height + margin.bottom - 20)
    .style("text-anchor", "middle")
    .style("font-size", "12px")
    .text("Transaction amount (unit: BGP)");

  // 
  layers.reverse().forEach((layer, layerIndex) => {
    svg.selectAll(`.bar-layer-${layerIndex}`)
      .data(layer.heights)
      .enter()
      .append("rect")
      .attr("class", `bar-layer-${layerIndex}`)
      .attr("x", (d, i) => x(i + 1))  //  x 
      .attr("y", d => y(d))
      .attr("width", x.bandwidth())
      .attr("height", d => height - y(d))
      .attr("fill", () => {
        switch (layerIndex) {
          // 1
          case 0: return "#8F5E0D"; // 5
          case 1: return "#B57F26"; // 4
          case 2: return "#D39E47"; // 3
          case 3: return "#F5C473"; // 2
          case 4: return "#FFDCA3"; // 1
          default: return "#cccccc"; // 
        }
      })
      .attr("opacity", 1) // 
      .on("mouseover", function() {
        // 
        svg.selectAll("rect")
          .attr("opacity", 0);
        d3.selectAll(`.bar-layer-${layerIndex}`)
          .attr("opacity", 1); // 

        //  X  segments
        xAxis.tickFormat((d, i) => {
          const segmentIndex = (numLayers - layerIndex - 1) * barsPerLayer + i;
          return segmentIndex < segments.length ? `${segments[segmentIndex]}` : '';
        });
        xAxisGroup.call(xAxis);
      })
      .on("mouseout", function() {
        // 
        svg.selectAll("rect")
          .attr("opacity", 1);

        //  X 
        xAxis.tickFormat(() => "");
        xAxisGroup.call(xAxis);
      });

    // 
    svg.selectAll(`.label-layer-${layerIndex}`)
      .data(layer.heights)
      .enter()
      .append("text")
      .attr("class", `label-layer-${layerIndex}`)
      .attr("x", (d, i) => x(i + 1) + x.bandwidth() / 2)
      .attr("y", d => y(d) - 10)
      .attr("dy", ".75em")
      .text((d, i) => {
        const segmentIndex = (numLayers - layerIndex - 1) * barsPerLayer + i;
        return segmentIndex < segments.length ? segments[segmentIndex] : '';
      })
      .style("text-anchor", "middle")
      .style("font-size", "10px")
      .on("mouseover", function() {
        // 
        svg.selectAll("rect")
          .attr("opacity", 0);
        d3.selectAll(`.bar-layer-${layerIndex}`)
          .attr("opacity", 1);

        //  X  segments
        xAxis.tickFormat((d, i) => {
          const segmentIndex = (numLayers - layerIndex - 1) * barsPerLayer + i;
          return segmentIndex < segments.length ? `${segments[segmentIndex]}` : '';
        });
        xAxisGroup.call(xAxis);
      })
      .on("mouseout", function() {
        // 
        svg.selectAll("rect")
          .attr("opacity", 1);

        //  X 
        xAxis.tickFormat(() => "");
        xAxisGroup.call(xAxis);
      });
  });
}

export function WidthLegendChart(containerSelector) {
  const width = 230; // 
  const height = 300;
  const margin = { top: 50, right: 20, bottom: 50, left: 70 };

  // SVG
  const svg = d3.select(containerSelector)
    .append("svg")
    .attr("width", width + margin.left + margin.right)
    .attr("height", height + margin.top + margin.bottom)
    .append("g")
    .attr("transform", `translate(${margin.left},${margin.top})`);

  // 
  const numLayers = 5;
  const barsPerLayer = Math.ceil(segmentWidths.length / numLayers); // 

  // 
  const layers = Array.from({ length: numLayers }, (_, layerIndex) => {
    return {
      widths: segmentWidths.slice(layerIndex * barsPerLayer, (layerIndex + 1) * barsPerLayer),
      segments: widthSegments.slice(layerIndex * barsPerLayer, (layerIndex + 1) * barsPerLayer),
      indices: Array.from({ length: barsPerLayer }, (_, i) => i + 1)
    };
  });

  // 
  const x = d3.scaleLinear()
    .domain([0, d3.max(segmentWidths)])
    .range([0, width]);

  const y = d3.scaleBand()
    .domain(layers[0].indices) // 
    .range([0, height])
    .padding(0.1);

  // 
  const xAxis = d3.axisBottom(x);
  const yAxis = d3.axisLeft(y)
    .tickFormat(() => ""); // 

  //  SVG
  svg.append("g")
    .attr("class", "x-axis")
    .attr("transform", `translate(0,${height})`)
    .call(xAxis);

  //  SVG
  const yAxisGroup = svg.append("g")
    .attr("class", "y-axis")
    .call(yAxis);

  // 
  layers.reverse().forEach((layer, layerIndex) => {
    svg.selectAll(`.bar-layer-${layerIndex}`)
      .data(layer.widths)
      .enter()
      .append("rect")
      .attr("class", `bar-layer-${layerIndex}`)
      .attr("x", 0)
      .attr("y", (d, i) => y(i + 1))  //  y 
      .attr("width", d => x(d))
      .attr("height", y.bandwidth())
      .attr("fill", () => {
        switch (layerIndex) {
          // 1
          case 0: return "#8F5E0D"; // 5
          case 1: return "#B57F26"; // 4
          case 2: return "#D39E47"; // 3
          case 3: return "#F5C473"; // 2
          case 4: return "#FFDCA3"; // 1
          default: return "#cccccc"; // 
        }
      })
      .attr("opacity", 1) // 
      .on("mouseover", function() {
        // 
        svg.selectAll("rect")
          .attr("opacity", 0);
        d3.selectAll(`.bar-layer-${layerIndex}`)
          .attr("opacity", 1); // 

        //  Y  segments
        yAxis.tickFormat((d, i) => {
          const segmentIndex = (numLayers - layerIndex - 1) * barsPerLayer + i;
          return segmentIndex < widthSegments.length ? `${widthSegments[segmentIndex]}` : '';
        });
        yAxisGroup.call(yAxis);
      })
      .on("mouseout", function() {
        // 
        svg.selectAll("rect")
          .attr("opacity", 1);

        //  Y 
        yAxis.tickFormat(() => "");
        yAxisGroup.call(yAxis);
      });

    // 
    svg.selectAll(`.label-layer-${layerIndex}`)
      .data(layer.widths)
      .enter()
      .append("text")
      .attr("class", `label-layer-${layerIndex}`)
      .attr("x", d => x(d) + 5) // 
      .attr("y", (d, i) => y(i + 1) + y.bandwidth() / 2)
      .attr("dy", ".35em")
      .text((d, i) => {
        const segmentIndex = (numLayers - layerIndex - 1) * barsPerLayer + i;
        return segmentIndex < widthSegments.length ? widthSegments[segmentIndex] : '';
      })
      .style("text-anchor", "start")
      .style("font-size", "10px")
      .on("mouseover", function() {
        // 
        svg.selectAll("rect")
          .attr("opacity", 0);
        d3.selectAll(`.bar-layer-${layerIndex}`)
          .attr("opacity", 1);

        //  Y  segments
        yAxis.tickFormat((d, i) => {
          const segmentIndex = (numLayers - layerIndex - 1) * barsPerLayer + i;
          return segmentIndex < widthSegments.length ? `${widthSegments[segmentIndex]}` : '';
        });
        yAxisGroup.call(yAxis);
      })
      .on("mouseout", function() {
        // 
        svg.selectAll("rect")
          .attr("opacity", 1);

        //  Y 
        yAxis.tickFormat(() => "");
        yAxisGroup.call(yAxis);
      });
  });

  // X
  svg.append("text")
    .attr("class", "x-axis-title")
    .attr("x", width / 2)
    .attr("y", height + margin.bottom - 10)
    .style("text-anchor", "middle")
    .style("font-size", "12px")
    .text("Mapping width (unit: px)");

  // Y
  svg.append("text")
    .attr("class", "y-axis-title")
    .attr("x", -50)
    .attr("y", -margin.left / 2 + 15)
    .style("text-anchor", "letf")
    .style("font-size", "12px")
    .text("Transaction amount (unit: BGP)");
}

export function AreaLegendChart(containerSelector) {
  const width = 340; // 
  const height = 400; // 
  const margin = { top: 40, right: 0, bottom: 50, left: 60 };

  //  SVG 
  const svg = d3.select(containerSelector)
    .append("svg")
    .attr("width", width + margin.left + margin.right)
    .attr("height", height + margin.top + margin.bottom)
    .append("g")
    .attr("transform", `translate(${margin.left},${margin.top})`);

  // 
  const x = d3.scaleLinear()
    .domain([0, width])
    .range([0, width]);

  const y = d3.scaleLinear()
    .domain([0, height])
    .range([height, 0]);

  // 
  const xAxis = d3.axisBottom(x).ticks(5).tickFormat(d => `${d}`);
  const yAxis = d3.axisLeft(y).ticks(5).tickFormat(d => `${d}`);

  //  SVG
  svg.append("g")
    .attr("class", "x-axis")
    .attr("transform", `translate(0,${height})`)
    .call(xAxis);

  //  y  SVG
  svg.append("g")
    .attr("class", "y-axis")
    .call(yAxis);

  //  x 
  svg.append("text")
    .attr("x", width / 2)
    .attr("y", height + margin.bottom - 10)
    .attr("text-anchor", "middle")
    .style("font-size", "14px")
    .text("Width (px)");

  //  y 
  svg.append("text")
    .attr("x", -height / 2)
    .attr("y", -margin.left + 20)
    .attr("transform", "rotate(-90)")
    .attr("text-anchor", "middle")
    .style("font-size", "14px")
    .text("Height (px)");

  // 
  const filteredAreaSizes = areaSizes.filter((_, index) => index % 3 === 0);
  const filteredAreaSegments = areaSegments.filter((_, index) => index % 3 === 0);

  // 
  const squares = filteredAreaSizes.map((size, index) => ({
    size,
    segment: filteredAreaSegments[index],
    side: Math.sqrt(size)
  }));

  // 
  squares.sort((a, b) => b.side - a.side);

  // 
  let xOffset = 0;
  let yOffset = 0;
  const padding = 20; // 

  // 
  squares.forEach((square, index) => {
    // 
    if (index > 0) {
      xOffset += padding;  // 
      yOffset += padding;  // 
    }

    // 
    if (xOffset + square.side > width) {
      xOffset = 0;  //  x 
      yOffset += padding + square.side;  // 
    }

    if (yOffset + square.side > height) {
      yOffset = 0;  //  y 
    }

    // 
    const group = svg.append("g")
      .attr("transform", `translate(${xOffset}, ${height - yOffset - square.side})`);

    // 
    group.append("rect")
      .attr("width", square.side)
      .attr("height", square.side)
      .attr("fill", "#8F5E0D")
      .attr("opacity", 0.9)
      .on("mouseover", function () {
        // 
        svg.selectAll("rect")
          .attr("opacity", 0.2);
        d3.select(this)
          .attr("opacity", 1); // 

        // 
        group.select("text")
          .attr("opacity", 1);
      })
      .on("mouseout", function () {
        // 
        svg.selectAll("rect")
          .attr("opacity", 0.9);

        // 
        group.select("text")
          .attr("opacity", 0);
      });

    //  areaSegments
    group.append("text")
      .attr("x", square.side / 2)
      .attr("y", square.side / 2)
      .attr("dy", ".35em")
      .text(`Amount <= ${square.segment}`)
      .style("text-anchor", "middle")
      .style("font-size", "10px")
      .style("fill", "white")
      .attr("opacity", 0); // 
  });
}
